import React from "react";
import styles from "./style.module.css";
import { BigContainer } from "../../style-app";
import Request from "../request/index";
import { Row, Col } from "react-grid-system";
import { useTranslation } from "react-i18next";

import productImg from "../../assets/newproduct/wear.jpg";

const ProductDetailsComponent = () => {
  const { t } = useTranslation();
  return (
    <>
      <div className={styles.product_details_section}>
        <BigContainer>
          <Row className={styles.details_row}>
            <Col className={styles.details_col} lg={8} md={6} xs={12} sm={12}>
              <div className={styles.details_wrapper}>
                <h4>
                  <span>CCО-00.4</span> <br />
                  <span>{t("ProductDetails.3")}</span>
                </h4>
                <ul className={styles.details_list}>
                  <li>
                    <div>
                      <p>{t("ProductDetails.4")}</p>
                    </div>
                    <div>
                      <p>Костюм</p>
                    </div>
                  </li>
                  <li>
                    <div>
                      <p>{t("ProductDetails.5")}</p>
                    </div>
                    <div>
                      <p>Куртка и полукомбинезон</p>
                    </div>
                  </li>
                  <li>
                    <div>
                      <p>{t("ProductDetails.6")}</p>
                    </div>
                    <div>
                      <p>Сигнальный</p>
                    </div>
                  </li>
                  <li>
                    <div>
                      <p>{t("ProductDetails.7")}</p>
                    </div>
                    <div>
                      <p>Oранжевый-темно синий</p>
                    </div>
                  </li>
                  <li>
                    <div>
                      <p>{t("ProductDetails.8")}</p>
                    </div>
                    <div>
                      <p>Смесовая, пл.200-240 г/м²</p>
                    </div>
                  </li>
                  <li>
                    <div>
                      <p>{t("ProductDetails.9")}</p>
                    </div>
                    <div>
                      <p>80%ПЭ/20%ХБ</p>
                    </div>
                  </li>
                  <li>
                    <div>
                      <p>{t("ProductDetails.10")}</p>
                    </div>
                    <div>
                      <p>
                        1 год с даты изготовления (при соблюдении условий
                        хранения)
                      </p>
                    </div>
                  </li>
                </ul>
              </div>
            </Col>
            <Col className={styles.details_col} lg={4} md={6} xs={12} sm={12}>
              <img src={productImg} alt="workwearimg" />
            </Col>
          </Row>
        </BigContainer>
      </div>
      <Request />
    </>
  );
};

export default ProductDetailsComponent;
